.statistics {
  color: black;
  width: 250px;
  background-color: goldenrod;
}

.statList {
  display: flex;
  justify-content: space-around;
  list-style: none;
  padding: 0;
}
.item {
  display: flex;
  flex-direction: column;
}
.title {
  text-align: center;
  text-transform: uppercase;
}
